<template>
  <div
    class="widgetContainer widgetContainer--scrollable  full-width transactionDetails"
    v-if="getSelectedTransaction">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <p class="title">
          {{ $t('transaction_details') }}
        </p>
        <span class="el-icon-close" @click="drawerClose" />
      </div>
    </div>
    
    <div class="widgetContainer__body">
      <div class="listWithAmount">
        <ul class="summary-box">
          <li class="cursor-auto">
            <div class="details trans-details">
              <p
                :class="
                  'amount ' +
                    (getSelectedTransaction.status == 'pending'
                      ? 'pending'
                      : getSelectedTransaction.txnType === 'debit'
                        ? 'minus'
                        : 'plus')
                ">
                {{ getSelectedTransaction.amount | transactionAmount(getSelectedAccount.currency) }}
              </p>

              <p class="label">
                {{ getSelectedTransaction.title }}
              </p>
              <p
                class="value"
                v-if="getSelectedTransaction.status == 'pending'">
                {{ $t('RCD_pending') }}
              </p>
              <p class="value" v-else>
                {{
                  getSelectedTransaction.txnDate
                    | date("MMM d, yyyy 'at' h:mm aa")
                }}
              </p>
            </div>
            <el-tag
              :class="'el-tag--blue transaction-type type-' + transferTypeTitle">
              {{ transferTypeTitle }}
            </el-tag>
          </li>
        </ul>
      </div>

      <div class="listSimple transactionDetails__other-details" v-if="data.description || data.paidTo">
        <h4 class="header-2">
          {{ $t('other_details') }}
        </h4>
        <ul>
          <!-- <li v-if="data.source && false">
            <p class="label">
              {{ $t('source') }}
            </p>
            <p class="value">
              {{ data.source }}
            </p>
          </li> -->
          
          <!-- <li v-if="data.transactionId && false">
            <p class="label">
              {{ $t('transactionId') }}
            </p>
            <p class="value">
              {{ data.transactionId }}
            </p>
          </li> -->
         

          <li v-for="d in otherDetails" :key="d.label">
            <p class="label">
              {{ d.label }}
            </p>
            <p class="value">
              {{ d.value }}
            </p>
          </li>
        </ul>
      </div>

      <div class="listWithActions transactionDetails__actions">
        <h4 class="header-2">
          {{ $t('actions') }}
        </h4>
        <ul>
          <li @click="getData(true)">
            {{ $t('viewPDF') }}
          </li>
          <li @click="openHelp">
            {{ $t('reportProblem') }}
          </li>
        </ul>
      </div>

      <div
        class="listSimple"
        v-if="
          getSelectedTransaction.transferType === 'card' &&
            getSelectedTransaction.card &&
            getSelectedTransaction.card.merchant
        ">
        <h4 class="header-2">
          {{ $t('location') }}
        </h4>
        <ul class="card-with-map">
          <div class="map">
            <GmapMap
              v-if="lat && lng"
              :center="{ lat, lng }"
              :zoom="7"
              map-type-id="terrain"
              style="width: 100%; height: 200px">
              <GmapMarker
                :position="{ lat, lng }"
                :clickable="true"
                :draggable="false" />
            </GmapMap>
          </div>
          <li>
            <p class="label">
              {{ $t('address') }}
            </p>
            <p class="value">
              {{ address }}
            </p>
          </li>
          <li>
            <p class="label">
              {{ $t('contact_country_title') }}
            </p>
            <p class="value">
              {{ getSelectedTransaction.card.merchant.merchantCountry }}
            </p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { gmapApi } from 'vue2-google-maps';
import { transferType } from '../enums/index';
export default {
  data() {
    return {
      lat: null,
      lng: null
    };
  },
  created() {
    if (this.getSelectedTransaction == null) {
      this.$router.push('/transaction/list');
    } else {
      setTimeout(() => {
        this.getData();
      }, 0);
    }
  },
  computed: {
    ...mapGetters('account', ['getSelectedAccount', 'getAllAccount']),
    ...mapGetters('transaction', ['getSelectedTransaction']),
    google: gmapApi,
    data() {
      let source, paidTo, receivedFrom, description, transactionId, location;
      /** @type {import('@m/transaction/store/state').transactionState['selectedTransaction']} */
      const transaction = this.getSelectedTransaction;
      switch (
        transaction.transferType //intrabank
      ) {
      case 'intrabank':
        source = transaction.accountId;
        paidTo = transaction.intrabank.contactName;
        description = transaction.description;
        transactionId = transaction.id;
        break;
      case 'ach':
        source = transaction.accountId;
        paidTo = transaction.ach.contactName;
        description = transaction.description;
        transactionId = transaction.id;
        break;
      case 'card':
        source = transaction.card.label;
        paidTo = transaction.card.merchant.merchantName;
        description = transaction.description;
        transactionId = transaction.id;
        break;
      default:
        description = transaction.description;
        break;
      }
      return {
        source,
        paidTo,
        receivedFrom,
        description,
        transactionId,
        location
      };
    },
    otherDetails() {
      let otherData = [];


      if(this.data.paidTo) {
        otherData.push(
          {
            label: this.$t('merchant'),
            value: this.data.paidTo
          }
        );
      }
      otherData.push(
         
        {
          label: this.$t('description'),
          value: this.data.description
        }
      )
      

      return otherData.filter(Boolean);
    },
    address() {
      const address = this.getSelectedTransaction.card.merchant;
      return (
        address.merchantCity +
        ',' +
        address.merchantState
      );
    },
    transferTypeTitle() {
      if (this.getSelectedTransaction) {
        if(this.getSelectedTransaction.subType === 'reward' && this.getSelectedTransaction.transferType === 'card') {
          return transferType['titles']['reward'];
        } else {
          return transferType['titles'][this.getSelectedTransaction.transferType] || this.getSelectedTransaction.transferType;
        }
      } else {
        return '';
      }
    },
  },
  methods: {
    ...mapActions('transaction', ['fetchTransaction']),
    
    getLatLong() {
      this.$gmapApiPromiseLazy().then(() => {
        if (
          this.google &&
          this.getSelectedTransaction.transferType === 'card' &&
          this.getSelectedTransaction.card &&
          this.getSelectedTransaction.card.merchant
        ) {
          const geocoder = new this.google.maps.Geocoder();
          const address = this.getSelectedTransaction.card.merchant;
          const addressString =
            address.merchantCity +
            ',' +
            address.merchantState +
            ',' +
            address.merchantCountry;

          geocoder.geocode(
            {
              address: addressString
            },
            (results, status) => {
              if (status == this.google.maps.GeocoderStatus.OK) {
                const latitude = results[0].geometry.location.lat();
                const longitude = results[0].geometry.location.lng();
                this.lat = latitude;
                this.lng = longitude;
              }
            }
          );
        }
      });
    },
    getData(isDownload = false) {
      const loader = this.showLoader();
      const method = 'fetchTransaction';
      this[method]({
        accountId: this.getSelectedAccount.id,
        transactionId: this.getSelectedTransaction.id,
        format: isDownload ? 'pdf' : 'json'
      })
        .then((res) => {
          if (res && isDownload) {
            const url = window.URL.createObjectURL(res);
            const a = document.createElement('a');
            a.href = url;
            a.download = `${this.getSelectedTransaction.id}-statement.pdf`;
            a.click();
            a.remove();
          }
          if (isDownload === false) {
            this.getLatLong();
          }
        })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          loader.close();
          this.isLoading = false;
        });
    },
    goToHome() {
      this.goToPreviousScreen();
    },
  }
};
</script>

